<template>
  <div>
    <div>
      <loading :active.sync="isLoading"></loading>
      <nav
        class="
          navbar navbar-expand-lg
          bg-snow
          navbar navbar-expand-md navbar-light
          fixed-top
        py-0
          navbar-fixed-right
          flex-nowrap
          border-bottom
        "
      >
    <a href="#" @click.prevent="onClickCategory('HOME')" class="logo"
    
    
    >JOJOLEE</a>

 <!-- 列子 -->
       <div class="collapse navbar-collapse ml-5" id="navbarSupportedContent">
          <ul class="navbar-nav h2">
            <li class="nav-item ml-3">
              <a
                href="#"
                class="nav-link text-lightOrange navbarFontSize"
                @click.prevent="onClickCategory('HOME')"
                :class="{ 'navbar-active': this.$route.path == '/' }"
                >HOME
                <i
                  class="fas fa-music"
                  style="display: none"
                  :class="{ 'icon-show': this.$route.path == '/' }"
                ></i
              ></a>
            </li>
            <li class="nav-item">
              <a
                href="#"
                class="nav-link text-lightOrange navbarFontSize"
                @click.prevent="onClickCategory('EG', 'Fender')"
                :class="{ 'navbar-active': this.$route.path === '/EG' }"
                >ELECTRIC
                <i
                  class="fas fa-music"
                  style="display: none"
                  :class="{ 'icon-show': this.$route.path == '/EG' }"
                ></i
              ></a>
            </li>
            <li class="nav-item ml-3">
              <a
                href="#"
                class="
                  nav-link
                  text-decoration-none text-lightOrange
                  navbarFontSize
                "
                @click.prevent="onClickCategory('AG', 'Martin')"
                :class="{ 'navbar-active': this.$route.path === '/AG' }"
                >ACOUSTIC
                <i
                  class="fas fa-music"
                  style="display: none"
                  :class="{ 'icon-show': this.$route.path == '/AG' }"
                ></i
              ></a>
            </li>
            <li class="nav-item pl-2 ml-2">
              <a
                href="#"
                class="
                  nav-link
                  text-decoration-none text-lightOrange
                  navbarFontSize
                "
                @click.prevent="onClickCategory('AC', 'Capo')"
                :class="{ 'navbar-active': this.$route.path === '/AC' }"
              >
                ACCESSORIES
                <i
                  class="fas fa-music"
                  style="display: none"
                  :class="{ 'icon-show': this.$route.path == '/AC' }"
                ></i
              ></a>
            </li>
          </ul>
        </div>

              <!-- 1CART -->
        <button
          id="navbar-Cart1"
          type="button"
          class="btn"
          data-toggle="modal"

          @click.prevent="OpenCartModal()"
          style="position: relative ; font-size:16px"
        >
          <i class="fas fa-shopping-cart fa-2x text-deepOrange" > </i>

          <span class="cart-itemnumber">{{ cartsLength }}</span>
        </button>

             <!-- 第二個CART-RWD -->
        <!-- 購物車直接去結帳 -->
   <div id="navbar-RWDCart" class="d-none ml-auto">

     <button
          
          type="button"
          class="btn border-0"
          style="position: relative"
          @click.prevent="GoToCheck()"
        >
          <i class="fas fa-shopping-cart fa-2x text-deepOrange"> </i>

          <span class="cart-itemnumber">{{ cartsLength }}</span>
        </button>



    <!-- <b-button ></b-button> -->
<button v-b-toggle.sidebar-backdrop class="btn" type="button"><i class="fas fa-bars "></i></button>
    <b-sidebar
      id="sidebar-backdrop"
      title="Sidebar with backdrop"
      :backdrop-variant="variant"
      backdrop
      shadow
    >
      <div class="px-3 py-2">
        <b-form-group label="Backdrop variant" label-for="backdrop-variant">
          <b-form-select id="backdrop-variant" v-model="variant" :options="variants"></b-form-select>
        </b-form-group>
      </div>
    </b-sidebar>








        </div>
<!-- navbar-RWDCart -->



      </nav>
      <!-- 位子 -->




<!-- 不要超過這裡 -->
      <!-- LISTRWD -->
      <nav class="navbar-expand-sm navbar-light bg-snow d-none mt-5">
        <div class="collapse navbar-collapse" id="navbarNavAltMarkup">
          <div class="navbar-nav">
            <ul
              class="
                navbar navbar-nav navbar-right
                text-center
                w-100
                d-block d-md-none
                nav-mobile
              "
            >
              <li class="nav-item">
                <a
                  href="#/"
                  class="nav-link router-link-active"
                  @click.prevent="onClickCategory('HOME')"
                  :class="{ 'navbar-active': this.$route.path == '/' }"
                  id="home"
                  >HOME</a
                >
              </li>
              <li class="nav-item">
                <a
                  data-toggle="collapse"
                  href="#girlsMenu"
                  class="nav-link"
                  :class="{ 'navbar-active': this.$route.path == '/EG' }"
                  @click.prevent="onClickCategory('EG', 'Fender')"
                >
                  Electric-Guitar
                </a>
              </li>
              <li class="nav-item">
                <a
                  data-toggle="collapse"
                  href="#girlsMenu"
                  class="nav-link"
                  :class="{ 'navbar-active': this.$route.path == '/AG' }"
                  @click.prevent="onClickCategory('AG', 'Martin')"
                >
                  Acoustic-Guitar
                </a>
              </li>
              <li class="nav-item">
                <a
                  data-toggle="collapse"
                  href="#girlsMenu"
                  class="nav-link"
                  :class="{ 'navbar-active': this.$route.path == '/AC' }"
                  @click.prevent="onClickCategory('AC', 'Capo')"
                >
                  Accessories
                </a>
              </li>
            </ul>
          </div>
        </div>
      </nav>


<!-- Cart -->
      <div
        class="modal fade"
        id="cartModal"
        tabindex="-1"
        role="dialog"
        aria-labelledby="cartModal"
        aria-hidden="true"
        style="z-index: 2040 !important"
      >
        <div
          class="modal-dialog modal-lg modal-dialog-centered"
          role="document"
          style="z-index: 2050 !important"
        >
          <div class="modal-content">
            <div class="modal-header border-bottom-0 bg-lightOrange">
              <h5 class="modal-title text-dark ml-3" id="cartModal">
                Your Shopping Cart
              </h5>
              <button
                type="button"
                class="close text-black"
                data-dismiss="modal"
                aria-label="Close"
              >
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body" v-if="total > 0">
              <table class="table table-image">
                <thead>
                  <tr class="font-weight-bold">
                    <th id="th1" scope="col" width="110"></th>
                    <th
                      scope="col"
                      width="300"
                      class="pl-3"
                      style="text-align: start !important"
                    >
                      Product
                    </th>
                    <th scope="col" width="150">Qty*Price</th>
                    <th scope="col" width="150">Total</th>
                    <th scope="col">Actions</th>
                  </tr>
                </thead>
                <tbody class="text-dark">
                  <tr v-for="item in cartProducts" :key="item.id">
                    <td>
                      <img
                        :src="item.product.imageUrl"
                        class="img-fluid"
                        style="max-height: 80px"
                        alt="Sheep"
                      />
                    </td>
                    <td
                      class="h6"
                      style="
                        text-align: start !important ;
                        vertical-align: middle;
                      "
                    >
                      <span>{{ item.product.title }}</span> <br />
                      <span>{{ item.feature }}</span>
                    </td>
                    <td style="vertical-align: middle">
                      {{ item.qty || 1 }}x{{ item.product.price }}
                    </td>
                    <td style="vertical-align: middle">
                      {{ item.final_total | currency }}
                    </td>
                    <td style="vertical-align: middle">
                      <a
                        href="#"
                        @click.prevent="deleteCartProduct(item.id)"
                        class="btn btn-danger btn-sm"
                      >
                        <i class="fa fa-times"></i>
                      </a>
                    </td>
                  </tr>
                </tbody>
              </table>
              <div class="d-flex justify-content-end">
                <h5>
                  Total:
                  <span class="price text-success">{{ total | currency }}</span>
                </h5>
              </div>
            </div>
            <div class="modal-body" v-if="total == 0">
              <div
                class="text-center text-dark py-4 border-bottom"
                style="letter-spacing: 1px; font-weight: bold"
              >
                <h2>購物車是空的歐</h2>
              </div>
            </div>
            <div
              class="modal-footer border-top-0 d-flex justify-content-between"
            >
              <button type="button" class="btn btn-light" data-dismiss="modal">
                Close
              </button>
              <button
                v-if="total > 0"
                type="button"
                class="btn  btn-primary text-snow"
                @click.prevent="GoToCheck()"
              >
                Checkout
              </button>
            </div>
          </div>
        </div>
      </div>



    </div>
  </div>
</template>

<script>
import $ from "jquery";
import LOGO from "../../../assets/img/indexBackground/LOGO.svg";
// import { Push } from 'vue-burger-menu'  // import the CSS transitions you wish to use, in this case we are using `Slide`

export default {
  data() {
    return {

variant: 'transparent',
        variants: [
          'transparent',
          'white',
          'light',
          'dark',
          'primary',
          'secondary',
          'success',
          'danger',
          'warning',
          'info',
        ],





      category: [],
      LOGO: LOGO,
      showqqq: false,
    };
  },

  methods: {
    // 購物車
    backtoTOP() {
            $("html,body").animate({ scrollTop: 0 }, 333);

    },

    OpenCartModal() {

console.log(this.$route.name)
      if (this.$route.name=="CheckOut") {
            this.$bus.$emit("message:push", "已經在購物車裡面摟", "success");
            return
      }else{
      $("#cartModal").modal("show");
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      vm.$http.get(api).then((response) => {
        vm.getCartAllProduct();
      });
      }
    },

    getCartAllProduct() {
      const vm = this;
      vm.$store.dispatch("getCartAllProduct");
    },

    deleteCartProduct(id) {
      const vm = this;
      vm.$store.dispatch("deleteCartProduct", id);
      this.$bus.$emit("message:push", "成功刪除", "danger");
        //  vm.$store.dispatch("getCartAllProduct");
         vm.getCartAllProduct()
 
    },

    // 結帳
    GoToCheck() {
      const vm = this;
      // if購物車是有內容就去 沒內容跳alert

      if (vm.cartsLength == 0) {
        this.$bus.$emit("message:push", "購物車是空的", "danger");
      } else {
        vm.$router.push({ path: `/CheckOut` });
        $("#cartModal").modal("hide");
      }
    },

    onClickCategory(category, grand) {
      // (EG,FENDER)
      const vm = this;
      let genderStr = "/" + category;

      //  vm.$router.push({ path: genderStr, query: { id: category }}).catch(err => {});
      vm.$router
        .push({ path: genderStr, query: { id: grand } })
        .catch((err) => {});
      // navbarNavAltMarkup
      // $("#na").modal("hide");

      $("html,body").animate({ scrollTop: 0 }, 333);
    },
    OpenRWDList() {
      $("#RWDList").modal("show");
    },
  },
  computed: {
    Cart() {
      return this.$store.state.Cart;
    },
    total() {
      return this.$store.state.total;
    },
    cartProducts() {
      return this.$store.state.cartProducts;
    },
    isLoading() {
      return this.$store.state.isLoading;
    },
    cartsLength() {
      return this.$store.state.cartsLength;
    },
  },
  // components: {
        
  //       Push // Register your component,
  
  //   },
  created() {
    this.getCartAllProduct();
  },
};
</script>

